import {PerformancePanelInitLayoutData} from '../../../../types';
export interface PerformancePanelProps {
	panelId: string;
	init_layout_data: PerformancePanelInitLayoutData;
}
// interface PerformanceParams {
// 	start: () => void;
// 	recording: Ref<boolean>;
// }

import {onBeforeMount} from 'vue';

export function SetupLayoutData(props: PerformancePanelProps /*, params: PerformanceParams*/) {
	onBeforeMount(() => {
		fromJSON(props.init_layout_data);
	});

	function fromJSON(json: PerformancePanelInitLayoutData) {
		// starting the performance monitor if it was started when the file was saved works fine.
		// But, it most likely will start after some nodes started cooking, since the scene is loaded first and the panel is lazily loaded.
		if (!json) {
			return;
		}
		// do not start the performance from here anymore,
		// as it is now done from the scene loader.
		// And doing it from here would do it too late for most nodes,
		// which would have started cooking by now
		// if (json.recording) {
		// 	console.warn('start perf from saved scene editor data');
		// 	params.start();
		// }
	}

	function layoutData() {
		return {
			// recording: params.recording.value,
		};
	}

	return {fromJSON, layoutData};
}
